@tailwind base;
@tailwind components;
@tailwind utilities;

@layer base {
    h1,
    .h1 {
        @apply mb-20 text-4xl;
    }

    body {
        @apply font-inter;
    }

    /* inter-300 - latin */
    @font-face {
        font-family: 'Inter';
        font-style: normal;
        font-weight: 300;
        src: url('./fonts/inter-v7-latin-300.eot'); /* IE9 Compat Modes */
        src: local(''),
            url('./fonts/inter-v7-latin-300.eot?#iefix')
                format('embedded-opentype'),
            /* IE6-IE8 */ url('./fonts/inter-v7-latin-300.woff2')
                format('woff2'),
            /* Super Modern Browsers */ url('./fonts/inter-v7-latin-300.woff')
                format('woff'),
            /* Modern Browsers */ url('./fonts/inter-v7-latin-300.ttf')
                format('truetype'),
            /* Safari, Android, iOS */
                url('./fonts/inter-v7-latin-300.svg#Inter') format('svg'); /* Legacy iOS */
    }
    /* inter-regular - latin */
    @font-face {
        font-family: 'Inter';
        font-style: normal;
        font-weight: 400;
        src: url('./fonts/inter-v7-latin-regular.eot'); /* IE9 Compat Modes */
