            url('./fonts/inter-v7-latin-regular.eot?#iefix')
                format('embedded-opentype'),
            /* IE6-IE8 */ url('./fonts/inter-v7-latin-regular.woff2')
                format('woff2'),
            /* Super Modern Browsers */
                url('./fonts/inter-v7-latin-regular.woff') format('woff'),
            /* Modern Browsers */ url('./fonts/inter-v7-latin-regular.ttf')
                format('truetype'),
            /* Safari, Android, iOS */
                url('./fonts/inter-v7-latin-regular.svg#Inter') format('svg'); /* Legacy iOS */
    }
    /* inter-600 - latin */
    @font-face {
        font-family: 'Inter';
        font-style: normal;
        font-weight: 600;
        src: url('./fonts/inter-v7-latin-600.eot'); /* IE9 Compat Modes */
