            url('./fonts/inter-v7-latin-600.eot?#iefix')
                format('embedded-opentype'),
            /* IE6-IE8 */ url('./fonts/inter-v7-latin-600.woff2')
                format('woff2'),
            /* Super Modern Browsers */ url('./fonts/inter-v7-latin-600.woff')
                format('woff'),
            /* Modern Browsers */ url('./fonts/inter-v7-latin-600.ttf')
                format('truetype'),
            /* Safari, Android, iOS */
                url('./fonts/inter-v7-latin-600.svg#Inter') format('svg'); /* Legacy iOS */
